.lban__navigation {
    display: flex;
    height: 100vh;
    flex: 0.13;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
    padding: 3rem 0;
    background: white;
}

.lban__logo {
    display: flex;
    padding: .5rem;
    height: 5rem;
    overflow: hidden;
    justify-content: center;
    align-items: center;

}

.lban__logo img {
    object-fit: contain;
    max-height: 4rem;
}

.lban__url {
    display: flex;
    padding: 2rem 1.5rem;
    padding-top: 5rem;
    flex-direction: column;
}

.lban__url ul {
    display: flex;
    list-style: none;
    justify-content: space-around;
    width: 100%;
    flex-direction: column;
}

.lban__url ul li {
    display: flex;
    list-style: none;
    justify-content: flex-start;
    height: 3rem;
    cursor: pointer;
    padding-left: 1rem;
    align-items: center;
    margin-bottom: 1rem;
}

.lban__url ul li:hover {
    background: var(--color2);
    color: black;
    border-radius: 10px;
}

.lban__url ul li a {
    display: flex;
    list-style: none;
    justify-content: space-between;
    text-decoration: none;
    color: black;
}

.lban__url ul li span {
    display: flex;
    list-style: none;
    justify-content: space-between;
}

.lban__url ul li a svg {
    margin-right: .5rem;
    margin-top: .1rem;
}

.lban__url ul li span svg {
    margin-right: .5rem;
    margin-top: .1rem;
}

.lban__url .active {
    background: var(--color1);
    color: white;
    border-radius: 10px;
}