@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');


:root {
  /* --lbanPurple: #9A198C; */
  --color1: #3AB29A;
  --color2: #39ABA6;
  --color3: crimson;
  --color4: #059B56
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

.appContainer {
  display: flex;
  flex: 1;
  height: 100vh;
  background: whitesmoke;
  justify-content: space-around;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.87;
  padding: .5rem 2rem;
  /* background: #FAFAFC; */

}

.white {
  background: white;
}

.forms {
  padding: 1rem 5rem;
}

.small {
  flex-basis: 33%;
}

.medium {
  flex-basis: 50%;
}

.large {
  flex-basis: 100%;
}

.group {
  flex-basis: 100%;
  display: flex;
  flex-direction: column;
}

.title {
  display: flex;
  padding: 1rem 2rem;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 10px;
  font-weight: bold;
  border-radius: 10px;
}

.nirsal__InputFlex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;

}

.nirsal__InputContainer {
  display: flex;
  flex-direction: column;
  margin-bottom: .5rem;
  padding: .5rem 2rem;
  justify-content: center;
}


.nirsal__InputContainerSM {
  display: flex;
  flex-direction: column;
  margin-bottom: .5rem;
  padding: .5rem 2rem;
}


.nirsal__InputContainer label {
  font-size: 13px;
  margin-left: .3rem;
  margin-bottom: .5rem;
}

.nirsal__InputContainer p {
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;

}

.nirsal__InputContainer input {
  width: 100%;
  height: 4rem;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.26);
  padding: .5rem 2rem;
  border: 1px solid gainsboro;
  border-radius: 10px;
  background: white;
}


.nirsal__InputContainer input[type="checkbox"] {
  width: 30px;
  height: 30px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.26);
  padding: .5rem 2rem;
  border: 1px solid gainsboro;
  border-radius: 10px;
  background: white;
  margin-right: 1rem;
}

.nirsal__InputContainer input:read-only {
  background: whitesmoke
}

.nirsal__InputContainer select {
  width: 100%;
  height: 4rem;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.26);
  background: white;
  padding: .5rem 2rem;
  border: 1px solid gainsboro;
  border-radius: 10px;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  background-size: 1em;

}

.nirsal__InputContainer textarea {
  width: 100%;
  height: 7rem;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.26);
  padding: .5rem 1.5rem;
  border-radius: 10px;
  background: white;
}

.nirsal__InputContainer span {
  display: flex;
  justify-content: center;
  align-items: center;
}

.nirsal__InputContainer input[type="radio"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  padding: 2px;
  background-clip: content-box;
  border: 1px solid gainsboro;
  border-radius: 50%;
  margin: 0 1rem;
}

.nirsal__InputContainer input[type="radio"]:checked {
  background-color: var(--nirsalGreen);
}


.nirsal__InputContainer button {
  width: 100%;
  height: 4rem;
  outline: none;
  padding: .5rem 2rem;
  border-radius: 10px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.nirsal__InputContainerSM button {
  width: 100%;
  height: 3rem;
  outline: none;
  padding: .5rem 2rem;
  border-radius: 10px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.most__RadioContainer {
  display: flex;
  /* justify-content: space-between; */
}

.btnContainer {
  display: flex;
  justify-content: flex-start;
  margin-bottom: .5rem;
  padding: 1rem 2rem;
}

.btn {
  outline: none;
  padding: .5rem 2rem;
  border-radius: 10px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 1.4rem;
}

.btn svg {
  margin: 0 .5rem;
}

.loading {
  height: 40px;
  width: 40px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0.4);
  border-top-color: #fff;
  border-bottom-color: #fff;
  animation: spinner3 800ms ease infinite;
}

@keyframes spinner3 {
  to {
    transform: rotate(360deg);
  }
}

button:disabled {
  background: whitesmoke;
  color: black;
  cursor: not-allowed;
}

.green {
  background: var(--color4);
  color: white;
}

.color3 {
  background: var(--color3);
  color: white;
}

.lightGreen {
  background: var(--color1);
  color: white;
}

.btnTable svg {
  margin-right: .5rem;
}

.btnTable {
  outline: none;
  padding: .5rem 1rem;
  border-radius: 10px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  font-size: 13px;
}


/* Forms */
/* CardFlex */
.cardFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.right {
  display: flex;
  justify-content: flex-end;
}

/* End of CardFlex */