.card {
    display: flex;
    flex-basis: 23%;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 2rem;
    justify-content: flex-start;
    border-radius: 5px;
}

.card h3 {
    font-size: 16px;
    /* color: var(--color1); */
    font-weight: bold;
}

.card p {
    font-size: 2rem;
}

.card:hover {
    transform: scale(1.1);
}

.blue {
    background: #0177FB;
    color: white;
}

.green {
    background: #019267;
    color: white;
}

.purple {
    background: #764AF1;
    color: white;
}

.yellow {
    background: #F8B400;
    color: white;
}


/* Media Query */

@media screen and (max-width: 1300px) {
    .card {
        flex-basis: 23%;
    }
}

@media screen and (max-width: 1050px) {
    .card {
        flex-basis: 31%;
    }
}

@media screen and (max-width: 1024px) {

    .card {
        flex-basis: 47%;
    }
}

@media screen and (max-width: 700px) {
    .card {
        flex-basis: 47%;
    }

}

@media screen and (max-width: 550px) {
    .card {
        flex-basis: 100%;
    }

}