.authContainer {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100vh;
    background: url('../../../assets/backdrop.jpg');
    background-size: cover;
    padding: 5rem;
}

.formContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.forms {
    display: flex;
    flex: 0.5;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.94);
    padding: 2rem 1rem;
    border-radius: 30px;
}

.forms input {
    width: 100%;
    height: 4rem;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.26);
    padding: .5rem 2rem;
    border: 1px solid gainsboro;
    border-radius: 30px;
    background: white;
}

.forms button {
    width: 100%;
    height: 4rem;
    outline: none;
    padding: .5rem 2rem;
    border-radius: 30px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.forms h3 {
    font-size: 1rem;
    text-align: center;
    color: black;
    font-weight: 500;
}

.forms p {
    font-size: .8rem;
    text-align: center;
    color: var(--color4);
}

.forms img {
    width: 30%;
    margin: 0 auto;
    margin-bottom: 1rem;
}

.designContainer {
    display: flex;
    flex: 0.3;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(120deg, rgba(58, 178, 154, 0.9), rgba(57, 171, 166, 0.9)), url('../../../assets/bg2.jpg');
    background-size: cover;
}